import React, { useEffect, useState } from 'react';
import { Link } from 'gatsby';
import BurgerBtn from './burgerBtn';

import {
  navigationStyle,
  menuStyle,
  btnStyle,
  menuOpenStyle,
  liStyle,
  linkStyle,
} from './burgerMenu.module.scss';

type NavItem = {
  link: string;
  title: string;
};

type Props = {
  open?: boolean;
  navItems: NavItem[];
};

const BurgerMenu: React.FC<Props> = ({ navItems, open: parentOpen }) => {
  const [open, setOpen] = useState(!!parentOpen);

  const toggleMenu = () => setOpen(!open);

  useEffect(() => {
    if (open) {
      if (CSS.supports('-webkit-touch-callout', 'none')) {
        document.body.classList.add('no-scroll');
      }
    } else {
      document.body.classList.remove('no-scroll');
    }
  }, [open]);

  useEffect(() => {
    window.addEventListener('resize', () => setOpen(false));
  });

  return (
    <nav className={navigationStyle}>
      <BurgerBtn
        className={btnStyle}
        open={open}
        onClick={toggleMenu}
        ariaLabel="Toggle the menu"
      />

      <ul className={`${menuStyle} ${open ? menuOpenStyle : ''}`}>
        {navItems.map(({ link, title }) => (
          <li key={link} className={liStyle}>
            <Link
              className={linkStyle}
              to={link}
              onClick={() => setOpen(false)}
            >
              {title}
            </Link>
          </li>
        ))}
      </ul>
    </nav>
  );
};

export default BurgerMenu;
