import * as React from 'react';

import { btnStyle, openStyle, barStyle } from './burgerBtn.module.scss';

type Props = {
  open: boolean;
  onClick: () => void;
  className?: string;
  ariaLabel?: string;
};

const BurgerBtn: React.FC<Props> = ({
  open,
  onClick,
  className,
  ariaLabel,
}) => (
  <button
    onClick={onClick}
    className={`${className} ${btnStyle} ${open ? openStyle : ''}`}
    aria-label={ariaLabel}
  >
    <span className={barStyle}></span>
  </button>
);

export default BurgerBtn;
